<script setup>
    import { ref } from 'vue';
    import Button from '../../../components/Button';
    import store from '../../../store';
    import formatMoney from '../../../utils/formatMoney.js';
    import post from '../../../utils/post';

    const selectedAction = ref('Withdraw');
    const input = ref('');
    const passport = ref('');

    const actions = ref({
        'Withdraw': 'Sacar',
        'Deposit': 'Depositar',
        'Transfer': 'Transferir',
    });

    function handleChangePage(page) {
        input.value = '';
        passport.value = '';
        selectedAction.value = page;
    }

    async function sendActionHandler(){
        const value = parseInt(Math.abs(input.value));
        if(selectedAction.value === 'Deposit'){
            const res = await post(`${selectedAction.value}`, { 
                value,
                targetId: (parseInt(Math.abs(passport.value)) || undefined)}
            );
            
            if(res) {
                store.state.initial.balance = res.balance;
                store.state.initial.transactions = res.transactions;
            }
            return;
        }

        if(store.state.initial.balance >= value){
            const res = await post(`${selectedAction.value}`, { 
                value,
                targetId: (parseInt(Math.abs(passport.value)) || undefined)}
            );
            
            if(res) {
                store.state.initial.balance = res.balance;
                store.state.initial.transactions = res.transactions;
            }
        }

        input.value = '';
        passport.value = '';
    }
</script>

<template>
    <div class="transaction-actions">
        <header>
            <span @click="handleChangePage('Withdraw')" 
            :class="{'active-action': selectedAction === 'Withdraw'}">Saque</span>
            
            <span @click="handleChangePage('Deposit')" 
            :class="{'active-action': selectedAction === 'Deposit'}">Depósito</span>
            
            <span @click="handleChangePage('Transfer')"
            :class="{'active-action': selectedAction === 'Transfer'}">Tranferência</span>
        </header>
        <div class="input-frame">
            <label for="value">Qual valor deseja {{actions[selectedAction].toLowerCase()}}?</label>
            <div class="input-row">
                <div class="input-list">
                    <span>$</span>
                    <input :style="{'max-width': selectedAction === 'Transfer' ? '7.5vw' : '14vw'}" class="inputValue" type="text" placeholder="0,00" v-model="input" name="Valor" id="value">
                    <input class="inputPassport" v-if="selectedAction === 'Transfer'" type="text" placeholder="Pass" v-model="passport">
                </div>
            </div>
            <Button :cb="sendActionHandler" :text="actions[selectedAction]"/>
        </div>
    </div>
</template>

